.container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 8px;
  box-shadow: inset 5px 5px 10px white, inset -5px -5px 10px skyblue;
}

.search-input {
  flex: 1;
  padding: 12px 15px;
  font-size: 1.1em;
  border: none;
  border-radius: 8px;
  outline: none;
  margin-right: 10px;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.search-input:focus {
  box-shadow: inset 2px 2px 10px rgba(0, 0, 0, 0.2);
}

.search-box button {
  background-color: #4ca550;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.search-box button:hover {
  background-color: #f1c40f; /* Yellow */
}

.video-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 15px;
  margin: 10px 0;
  background: #f0f0f0;
  text-align: center;
  width: 100%;
  max-width: 600px;
}

.video-title {
  background: #c5f14a;
  font-size: 1.2em;
  border-radius: 10px;
  padding: 10px;
  box-shadow: inset 5px 5px 10px #8c8c8c, inset -5px -5px 10px #dcdcdc;
}

.container-video {
  display: flex;
  flex-direction: row;
  padding: 20px;
  margin: 20px;
}

.button-type {
  background-color: #4ca550;
  color: white;
  padding: 10px 20px;
  margin-left: 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button-type:hover {
  background-color: #f1c40f; /* Yellow */
}

.developer-info{
  background-color: yellowgreen;
  padding:5px;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  border-radius: 15px 50px;
}
.video-player {
  margin-top: 20px;
  padding: 10px;
 /* border: 10px solid #02ec0e;  Green border */
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2); /* Add some shadow for effect */
  background-color: #f0f0f0; /* Optional background color */
  border: 8px solid;
  border-image: repeating-linear-gradient(to bottom right, #33475b, #0033CC, #FF77CC, rgb(255, 122, 89)) 20;
}

.video-player iframe {
  width: 100%;
  height: 300px; /* Adjust based on your needs */
  border: none; /* Remove default iframe border */
  border-radius: 8px;
}

